<template>
  <div class='container'>
    <!-- ===============整体盒子卡片=============== -->
    <el-card class="article-card" :body-style="{ color: 'red' }">

      <!-- ==================================表头筛选表格================================== -->
      <el-form inline>
        <!-- ====关键词搜索===1= -->
        <el-form-item label-width="80px" label="关键字">
          <el-input style="width:188px" placeholder="根据文章标题搜索" v-model="title"></el-input>
        </el-form-item>

        <!-- ====状态搜索==== -->
        <el-form-item label-width="80px" label="状态">
          <el-select v-model="state">
            <el-option label="启用" value="1"></el-option>
            <el-option label="禁用" value="0"></el-option>
          </el-select>
        </el-form-item>

        <!-- ====按钮==== -->
        <el-form-item>
          <!-- ==清除按钮== -->
          <el-button size="small" @click="state='';title=''">清除</el-button>
          <!-- ==搜索按钮== -->
          <el-button type="primary" size="small" @click="sift()">搜索</el-button>
        </el-form-item>

        <!-- ====新增按钮1==== -->
        <el-form-item label-position="right" class="addButton">
          <el-button type="success" size="small" @click="addFunction"><i class="el-icon-edit"></i>新增技巧</el-button>
        </el-form-item>
      </el-form>

      <!-- ==================================条数总数==================================-->
      <el-alert :title="'数据一共'+$store.state.questionsChoice.articles.counts+'条'" type="info" show-icon :closable="false" style="margin-bottom: 15px;"></el-alert>

      <!-- ==================================数据表格==================================-->
      <el-table :data="tableData" style="width: 100%"
        :header-cell-style="{ background: '#fafafa', color: '#909399', 'border-bottom': '2px solid #e8e8e8' }"
        :row-style="{ height: '67px', }">

        <el-table-column label="序号" min-width="80px" :cell-style="{ 'text-algin': 'left' }" align="left">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ (page-1)*pageSize+(scope.$index + 1) }}</span>
          </template>
        </el-table-column>

        <el-table-column label="文章标题" min-width="400px" align="left">
          <template slot-scope="scope">
            <span style="margin-left: 10px">
              {{ scope.row.title }}
              <el-link type="primary" icon="el-icon-film" style="font-size: 18px;color:#0000ff" :underline="false" v-if="scope.row.videoURL
                " @click="aa(scope.row.videoURL)"></el-link>
            </span>
            <!-- <video src="scope.row.videoURL" v-if="open"></video> -->
          </template>

        </el-table-column>

        <el-table-column label="阅读数" min-width="124px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.visits }}</span>
          </template>
        </el-table-column>

        <el-table-column label="录入人" min-width="122px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.username }}</span>
          </template>
        </el-table-column>

        <el-table-column label="录入时间" min-width="122px" align="left">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.createTime | parseTimeByString }}</span>
          </template>
        </el-table-column>

        <el-table-column label="状态" min-width="100px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.state === 1 ? '已启用' : '已禁用' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" min-width="180px">
          <template slot-scope="scope">
            <div class="function">
              <el-link type="primary" :underline="false" @click="fn(scope)">预览</el-link>
              <el-link type="primary" :underline="false" @click="changeState(scope.row.id,scope.row.state)">{{ scope.row.state === 0 ? '启用' : '禁用' }}</el-link>
              <el-link type="primary" :underline="false" :disabled="scope.row.state === 1" @click="changeFunction(scope)">修改</el-link>
              <el-link type="primary" :underline="false" :disabled="scope.row.state === 1" @click="delFunction(scope.row.id)">删除</el-link>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="Pagination"  v-if="$store.state.questionsChoice.articles.counts>0">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page"
          :page-sizes="[5, 10, 20, 30]" :page-size="pageSize" layout="  prev, pager,next,sizes,jumper"
          :total="$store.state.questionsChoice.articles.counts" background>
        </el-pagination>
      </div>
    </el-card>
    <!-- ====预览页面==== -->
    <el-dialog :visible.sync="viewShow" title="预览文章" width="60%">
      <div class="addBoxBody">
        <div class="articleTitle">
          <h2>{{ viewTitle }}</h2>
          <span>{{ viewCreateTime | parseTimeByString }}</span>
          <span>{{ viewUserName }}</span>
          <span><i class="el-icon-view" style="margin-right: 10px;"></i>{{ viewUserVisits }}
          </span>
        </div>
        <div class="addBoxContent" style="background-color: #f5f5f5;">
          <div v-html="viewHtml"></div>
        </div>
      </div>
    </el-dialog>
    <!-- ====预览页面结束==== -->

    <!-- ====新增页面1==== -->
    <el-dialog :visible.sync="addShow" :title="formTitle" width="58%" @close="closeDialog">

      <div class="addShow">

        <el-form :model="formData" :rules="addRules" ref="formData" label-width="100px">
          <el-form-item label="文章标题" prop="title">
            <el-input v-model="formData.title" placeholder="请输入文章标签"></el-input>
          </el-form-item>

          <el-form-item label="文章内容" prop="articleBody">
            <quill-editor v-model="formData.articleBody" :options="options"></quill-editor>
          </el-form-item>

          <el-form-item label="视频地址">
            <el-input v-model="formData.videoURL" placeholder="请输入视频地址"></el-input>
          </el-form-item>

        </el-form>

      </div>
      <span slot="footer" class="dialog-footer" style="display: flex;justify-content: end;">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <!-- ====新增页面结束==== -->
    <!-- ========================视频==================== -->
    <div class="mask" v-if="videoShow">
            <!-- 关闭按钮 -->
            <div class="close-vedio" @click="videoShow = false"><i class="el-icon-close"></i></div>
            <!-- 视频 -->
            <div class="video">
              <video :src="videoURL" width="800px" height="600px" controls autoplay></video>
            </div>
          </div>
  </div>
</template>

<script>
import { createAPI } from '@/utils/request.js'
import { mapActions } from 'vuex'
import { update, remove } from '@/api/hmmm/articles'

// ================富文本编辑器设置
const toolbarOptions = [ // 工具栏想要显示什么，就要在这里加上什么
  ['bold', 'italic', 'underline', 'strike'], // 粗体、斜体、下划线、删除线
  [{ list: 'ordered' }, { list: 'bullet' }], // 有序列表、无序列表
  ['blockquote'],
  ['code-block', 'image', 'link'] // 链接、图片、视频
]

export default {
  data () {
    return {
      tableData: [],
      page: 1,
      pageSize: 10,
      title: '',
      state: '',
      open: false,
      videoShow: false,
      viewShow: false,
      viewTitle: '',
      viewCreateTime: '',
      viewUserName: '',
      viewUserVisits: '',
      viewHtml: '',
      addShow: false,
      formTitle: '',
      articeId: '',
      videoURL: '',
      formData: {
        title: '',
        articleBody: '',
        videoURL: ''
      },
      addRules: {
        title: [{ required: true, message: '请输入文章内容', trigger: 'blur' }],
        articleBody: [{ required: true, message: '请输入文章内容', trigger: 'blur' }]
      },
      options: {
        placeholder: '',
        modules: {
          toolbar: toolbarOptions
        }
      }
    }
  },
  async created () {
    await this.getArticleListAsync()
    // const res = await createAPI('articles', 'get', { page: 1, pagesiaze: 10 })
    // console.log(this.$store.state.questionsChoice.articles.ArticleList)
    this.tableData = this.$store.state.questionsChoice.articles.ArticleList
  },
  methods: {
    ...mapActions('questionsChoice/articles', ['getArticleListAsync']),
    aa (url) {
      this.videoShow = true
      this.videoURL = url
      console.log(url)
    },
    // ======更改每页个数
    async handleSizeChange (val) {
      this.page = 1
      this.pageSize = val
      await this.getArticleListAsync({ page: this.page, pagesize: val })
      this.tableData = this.$store.state.questionsChoice.articles.ArticleList
    },
    // ======更改页数=========
    async handleCurrentChange (val) {
      this.page = val
      await this.getArticleListAsync({ page: val, pagesize: this.pageSize })
      this.tableData = this.$store.state.questionsChoice.articles.ArticleList
    },
    // ======筛选==========
    async sift () {
      if (this.state) {
        await this.getArticleListAsync({ page: 1, pagesize: this.pageSize, keyword: this.title, state: this.state })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
      } else {
        await this.getArticleListAsync({ page: 1, pagesize: this.pageSize, keyword: this.title })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
      }
    },
    // 预览功能
    fn (data) {
      // console.log(data)
      this.viewShow = true
      this.viewTitle = data.row.title
      this.viewCreateTime = data.row.createTime
      this.viewUserName = data.row.username
      this.viewUserVisits = data.row.visits
      this.viewHtml = data.row.articleBody
    },
    // 弹出新增文章的页面
    addFunction () {
      this.formData.title = ''
      this.formData.articleBody = ''
      this.formData.videoURL = ''
      this.addShow = true
      this.formTitle = '新增文章'
    },
    // 弹出修改文章的页面
    changeFunction (data) {
      this.addShow = true
      this.formTitle = '修改文章'
      // 修改文章的数据回显
      this.formData.title = data.row.title
      this.formData.articleBody = data.row.articleBody
      this.formData.videoURL = data.row.videoURL
      this.formId = data.row.id
    },
    closeDialog () {
      this.$refs.formData.resetFields()
      this.formData.title = ''
      this.formData.articleBody = ''
      this.formData.videoURL = ''
    },
    // =====================================新增和修改文章的公用弹框====================================
    async submit () {
      if (this.formTitle === '新增文章') {
        this.$refs.formData.validate(async (pass) => {
          if (pass) {
          // 添加请求
            await createAPI('/articles', 'post', this.formData)
            // 重新渲染
            this.page = 1
            await this.getArticleListAsync({ page: this.page, pagesize: this.pageSize, keyword: this.title })
            this.tableData = this.$store.state.questionsChoice.articles.ArticleList
            this.addShow = false
            // 消息提示
            this.$message({
              message: '操作成功',
              type: 'success'
            })
            // 清空表单
            this.formData.title = ''
            this.formData.articleBody = ''
            this.formData.videoURL = ''
          } else {

          }
        })
      } else if (this.formTitle === '修改文章') {
        // 修改请求1
        await update({
          id: this.formId,
          title: this.formData.title,
          articleBody: this.formData.articleBody,
          videoURL: this.formData.videoURL
        })
        // 重新渲染
        await this.getArticleListAsync({ page: this.page, pagesize: this.pageSize, keyword: this.title })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
        // 关闭弹框
        this.addShow = false
        // 消息提示
        this.$message({
          message: '操作成功',
          type: 'success'
        })
        // 清空表单
        this.formData.title = ''
        this.formData.articleBody = ''
        this.formData.videoURL = ''
      }
    },
    // ====删除文章
    delFunction (id) {
      this.$confirm('此操作将永久删除该文章, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        remove({ id })
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        // 重新渲染
        await this.getArticleListAsync({ page: this.page, pagesize: this.pageSize, keyword: this.title })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
        // 重新渲染
        await this.getArticleListAsync({ page: this.page, pagesize: this.pageSize, keyword: this.title })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 修改文章状态
    async changeState (id, state) {
      // console.log(state)
      if (state === 0) {
        await createAPI(`/articles/${id}/1`, 'post')
        // 重新渲染
        await this.getArticleListAsync({ page: this.page, pagesize: this.pageSize, keyword: this.title })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
        // 消息提示
        this.$message({
          message: '操作成功',
          type: 'success'
        })
      } else {
        await createAPI(`/articles/${id}/0`, 'post')
        // 重新渲染
        await this.getArticleListAsync({ page: this.page, pagesize: this.pageSize, keyword: this.title })
        this.tableData = this.$store.state.questionsChoice.articles.ArticleList
        // 消息提示
        this.$message({
          message: '操作成功',
          type: 'success'
        })
      }
    },
    close () {
      this.addShow = false
      this.formData.title = ''
      this.formData.articleBody = ''
      this.formData.videoURL = ''
    }
  }
}
</script>

